<template>
  <div>
    <!-- Small boxes (Stat box) -->
    <div class="row">
      <div class="col-lg-3 col-6">
        <Penal />
      </div>
      <div class="col-lg-3 col-6">
        <ViolenciaGeneroPorcentaje />
      </div>
      <div class="col-lg-3 col-6">
        <Contravencional />
      </div>
      <div class="col-lg-3 col-6">
        <Faltas />
      </div>
    </div>
    <!-- /.row -->

    <!-- Main row -->
    <div class="row">
      <!-- Left col -->
      <section class="col-lg-6">
        <!-- Custom tabs (Charts with tabs)-->
        <ResolucionesMes />
      </section>
      <!-- /.Left col -->

      <!-- right col -->
      <section class="col-lg-6">
        <ResolucionesTipos />
      </section>
      <!-- right col -->
    </div>
    <!-- /.row (main row) -->

    <!-- 2 row -->
    <div class="row">
      <!-- Left col -->
      <section class="col-lg-6">
        <!-- Custom tabs (Charts with tabs)-->
        <RankingArticulos />
      </section>
      <!-- /.Left col -->

      <!-- right col -->
      <section class="col-lg-6">
        <ConductaArticulos />
      </section>
      <!-- right col -->
    </div>
    <!-- /.row (2 row) -->
  </div>
</template>

<script>
import { mapMutations } from "vuex";
// Info Box
//import ResolucionesTotal from "@/components/resoluciones/Total";
import Contravencional from "@/components/resoluciones/Contravencional";
import Penal from "@/components/resoluciones/Penal";
import Faltas from "@/components/resoluciones/Faltas";
//import ViolenciaGenero from "@/components/resoluciones/ViolenciaGenero";
import ViolenciaGeneroPorcentaje from "@/components/resoluciones/ViolenciaGeneroPorcentaje";

// Graficos
import ResolucionesMes from "@/components/resoluciones/graficos/ResolucionesMes";
import ResolucionesTipos from "@/components/resoluciones/graficos/ResolucionesTipos";
import RankingArticulos from "@/components/resoluciones/graficos/RankingArticulos";
// Tablas
import ConductaArticulos from "@/components/resoluciones/tablas/ConductaArticulos";

export default {
  components: {
    // Info Box
    Contravencional,
    Penal,
    Faltas,
    ViolenciaGeneroPorcentaje,

    // Graficos
    ResolucionesMes,
    ResolucionesTipos,
    RankingArticulos,
    // Tablas
    ConductaArticulos,
  },
  created() {
    this.setParams();
  },
  methods: {
    ...mapMutations(["SET_YEAR", "SET_TITLE"]),
    setParams() {
      this.SET_YEAR(this.$route.params.year);
      this.SET_TITLE(
        this.$i18n.t("resoluciones") + " " + this.$route.params.year
      );
    },
  },
  watch: {
    $route() {
      this.setParams();
    },
  },
};
</script>

